export default class Vector2d {
    constructor(_x, _y) {
        this._x = _x;
        this._y = _y;
    }
    get x() {
        return this._x;
    }
    get y() {
        return this._y;
    }
    static isSame(a, b) {
        return a.x === b.x && a.y === b.y;
    }
}



