import { Direction } from './enums';
import Maze from './maze';
import Vector2d from './vector2d';
export const moveEntity = (entity, speed, state) => {
    const { position, target, direction, nextDirection } = entity;
    let { x, y } = target;
    const maxX = Maze.maxX(state);
    const maxY = Maze.maxY(state);
    if (shouldUpdateTarget(entity)) {
        const newDir = Maze.canMove(getNextPosition(nextDirection, target), state) && nextDirection !== direction;
        if (newDir) {
            entity.direction = nextDirection;
        }
        switch (newDir ? nextDirection : direction) {
            case Direction.Left:
                x--;
                break;
            case Direction.Right:
                x++;
                break;
            case Direction.Up:
                y--;
                break;
            case Direction.Down:
                y++;
                break;
        }
        if (x < 0) {
            x = maxX;
        }
        else if (x > maxX) {
            x = 0;
        }
        if (y < 0) {
            y = maxY;
        }
        else if (y > maxY) {
            y = 0;
        }
        const newPos = new Vector2d(x, y);
        if (Maze.canMove(newPos, state)) {
            entity.target = newPos;
        }
    }
    else {
        let { x, y } = position;
        switch (direction) {
            case Direction.Left:
                x -= speed;
                x = Math.max(x, target.x);
                break;
            case Direction.Right:
                x += speed;
                x = Math.min(x, target.x);
                break;
            case Direction.Up:
                y -= speed;
                y = Math.max(y, target.y);
                break;
            case Direction.Down:
                y += speed;
                y = Math.min(y, target.y);
                break;
        }
        if (x < 0) {
            x = maxX;
        }
        else if (x > maxX) {
            x = 0;
        }
        if (y < 0) {
            y = maxY;
        }
        else if (y > maxY) {
            y = 0;
        }
        entity.position = new Vector2d(x, y);
    }
};
const shouldUpdateTarget = (entity) => {
    return Vector2d.isSame(entity.position, entity.target);
};
export const getNextPosition = (nextDirection, target) => {
    let { x, y } = target;
    switch (nextDirection) {
        case Direction.Right:
            x++;
            break;
        case Direction.Left:
            x--;
            break;
        case Direction.Down:
            y++;
            break;
        case Direction.Up:
            y--;
            break;
    }
    return new Vector2d(x, y);
};
export default class Entity {
    constructor(_position, _target, _direction) {
        this._position = _position;
        this._target = _target;
        this._direction = _direction;
        this._isDead = false;
        this._nextDirection = _direction;
    }
    get position() {
        return this._position;
    }
    set position(value) {
        this._position = value;
    }
    get target() {
        return this._target;
    }
    set target(value) {
        this._target = value;
    }
    get direction() {
        return this._direction;
    }
    set direction(value) {
        this._direction = value;
    }
    get nextDirection() {
        return this._nextDirection;
    }
    set nextDirection(value) {
        this._nextDirection = value;
    }
    get isDead() {
        return this._isDead;
    }
    set isDead(value) {
        this._isDead = value;
    }
}



